/* eslint-disable no-console */
console.log(
	'Hello World! (from create-block-content-seven-second-test block)'
);
/* eslint-enable no-console */
let btnStart = document.querySelector('.modal-seven-second-test-img');
let modalImg = document.querySelector('.modal-content.modal-seven-second-test-img');
let modalTest = document.querySelector('.modal-content.modal-seven-second-test-questions');
let whiteBg = document.querySelector('.activate-on-button-click');

btnStart.addEventListener('click', function() {
  setTimeout(showTest, 7000);
});

function showTest() {
	whiteBg.style.opacity = 1;
  modalTest.classList.add('activate-modal');
  modalImg.classList.remove('activate-modal');
}
